*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --color-background: #84a178;
  --color-primary-text: #fcfcfc;
  --color-secondary-text: #131313;
  --color-text-orange: #d6936d;
  --color-btn-default: #f8d24e;
  --color-btn-hover_focus: #0a8817;
  --color-btn-disable: #ababab;
  --color-mobile-menu: #85a279;
  --color-btn-slider: #d9d9d9;
  --color-bg-modal: rgba(156, 190, 143, .7);
  --tint-icecream: #9cbe8f;
  --tint-icecoffee: #efd478;
  --tint-milkshake: #d6936d;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

body {
  background: var(--color-background);
  letter-spacing: .04em;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

a:link, a:visited {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.btn {
  text-align: center;
  height: 42px;
  background-color: var(--color-btn-default);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-header_horisontal {
  padding-top: 16px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .page-header_horisontal {
    gap: 32px;
    padding-top: 24px;
  }
}

@media screen and (min-width: 1200px) {
  .page-header_horisontal {
    justify-content: space-between;
    padding-top: 8px;
  }
}

.menu-open-btn {
  background-color: var(--color-background);
  cursor: url("cursor.e4f2bfcc.png"), auto;
  border: none;
  margin-left: auto;
  line-height: 0;
}

@media screen and (min-width: 1200px) {
  .menu-open-btn {
    display: none;
  }
}

.page-navigation {
  display: none;
}

@media screen and (min-width: 1200px) {
  .page-navigation {
    padding-top: 32px;
    display: flex;
  }
}

.btn-buy-now_mobile-hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  .btn-buy-now_mobile-hidden {
    display: flex;
  }
}

.btn-buy-now_mobile-visible {
  display: flex;
}

@media screen and (min-width: 768px) {
  .btn-buy-now_mobile-visible {
    display: none;
  }
}

.mobile-menu {
  width: 100vw;
  height: 100%;
  z-index: 2;
  background-color: var(--color-background);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: transform .75s cubic-bezier(.4, 0, .2, 1), opacity .75s cubic-bezier(.4, 0, .2, 1), visibility .75s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}

.mobile-close-btn {
  cursor: url("cursor.e4f2bfcc.png"), auto;
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  line-height: 0;
  position: absolute;
  top: 16px;
  right: 20px;
}

.mobile-close-icon {
  color: var(--color-primary-text);
  fill: currentColor;
}

.mobile-menu-container {
  height: 100%;
  flex-direction: column;
  padding-top: 12px;
  padding-left: 31px;
  display: flex;
  position: relative;
}

.mobile-navigation {
  flex-direction: column;
  align-items: center;
  gap: 32px;
  margin-top: 51px;
  margin-bottom: 111px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .mobile-navigation {
    gap: 40px;
    margin-top: 76px;
  }
}

.mobile-navigation__link {
  color: var(--color-primary-text);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.21875;
}

@media screen and (max-width: 319px) {
  .mobile-navigation__link {
    font-size: 10vw;
  }
}

@media screen and (min-width: 768px) {
  .mobile-navigation__link {
    font-size: 48px;
    font-weight: 700;
    line-height: 1.22917;
  }
}

.mobile-navigation__link:hover, .mobile-navigation__link:focus, .mobile-navigation__link_black {
  color: var(--color-secondary-text);
}

.btn-buy-now {
  background-color: var(--color-btn-default);
  cursor: url("cursor.e4f2bfcc.png"), auto;
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-self: center;
  align-items: center;
  gap: 7px;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21429;
}

@media screen and (min-width: 1200px) {
  .btn-buy-now {
    align-self: flex-end;
    margin-bottom: 2px;
  }
}

.btn-buy-now:hover, .btn-buy-now:focus {
  background-color: var(--color-btn-hover_focus);
}

.btn-buy-now:focus, .icon-buy:focus {
  color: var(--color-primary-text);
  fill: currentColor;
}

.menu-navigation {
  justify-content: center;
  gap: 27px;
  display: flex;
}

.menu-navigation__link {
  color: var(--color-primary-text);
  cursor: url("cursor.e4f2bfcc.png"), auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.menu-navigation__link:hover, .menu-navigation__link:focus {
  color: var(--color-btn-default);
}

.logo {
  max-width: 41px;
  cursor: url("cursor.e4f2bfcc.png"), auto;
}

@media screen and (min-width: 48px) {
  .logo {
    max-width: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .logo {
    max-width: 60px;
  }
}

.customer-reviews {
  background: #fcfcfc;
}

.slider {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.slider__block {
  flex-direction: column;
}

.slider__img {
  width: 85px;
  height: 85px;
  border-radius: 45%;
  margin-left: auto;
  margin-right: auto;
}

.slider__review {
  text-align: center;
  color: var(--color-primary-text);
  background: var(--color-background);
  border-radius: 10px;
  align-items: center;
  display: flex;
}

.slider__customer {
  text-align: center;
  color: var(--color-secondary-text);
  margin-bottom: 14px;
  font-weight: 500;
  line-height: 186%;
}

.slick-dots li button {
  width: 12px;
  height: 12px;
  cursor: pointer;
  color: rgba(0, 0, 0, 0);
  background: none;
  border: 0;
  outline: none;
  padding: 4px;
  font-size: 0;
  line-height: 0;
  display: block;
}

.slick-dots li {
  width: 12px;
  height: 12px;
  cursor: pointer;
  margin: 0 5px;
  padding: 0;
  display: inline-block;
  position: relative;
}

.slick-dots li button:before {
  width: 12px;
  height: 12px;
  content: "•";
  text-align: center;
  color: var(--color-btn-slider);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: slick;
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.slick-dots li.slick-active button:before {
  color: var(--color-background);
}

@media screen and (max-width: 767px) {
  .customer-reviews {
    padding-bottom: 420px;
  }

  .slider__review {
    width: 280px;
    height: 256px;
    margin: 32px auto 30px;
    padding-left: 39px;
    padding-right: 39px;
    font-size: 12px;
    line-height: 255%;
  }

  .slider__customer {
    margin-bottom: 14px;
    font-size: 20px;
  }
}

@media screen and (min-width: 768px) {
  .customer-reviews {
    padding-bottom: 427px;
  }

  .slider__review {
    width: 705px;
    height: 138px;
    margin: 28px auto 32px;
    padding: 28px 80px;
    font-size: 14px;
    font-weight: 500;
    line-height: 186%;
  }

  .slider__customer {
    margin-bottom: 32px;
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .customer-reviews {
    padding-bottom: 207px;
  }

  .slider__review {
    width: 968px;
    height: 138px;
    margin: 32px auto;
    padding: 39px 74px;
    font-size: 16px;
    font-weight: 500;
    line-height: 186%;
  }
}

.contacts__block {
  background: var(--color-primary-text);
  border-radius: 24px;
  padding: 40px 32px;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contacts__block:not(:last-child) {
  margin-bottom: 20px;
}

.contacts__block:last-child {
  margin-bottom: 32px;
}

.contacts__category {
  background: var(--color-btn-default);
  color: var(--color-secondary-text);
  border-radius: 4px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 25px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.83333;
  display: inline-block;
}

.contacts__city {
  color: var(--color-secondary-text);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.83333;
}

.contacts__adress {
  color: var(--color-text-orange);
  margin-bottom: 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.83333;
  display: block;
}

.contacts__adress:hover {
  color: var(--color-text-orange);
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.contacts__adress:focus {
  color: var(--color-text-orange);
  outline: solid 1px var(--color-text-orange);
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
}

.contacts__schedule {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
  margin-bottom: 24px;
  padding: 24px 0;
}

.contacts__working-period:first-child {
  margin-bottom: 8px;
}

.contacts__working-days {
  color: var(--color-secondary-text);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.83333;
}

.contacts__working-hours {
  color: var(--color-btn-disable);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.83333;
}

.contacts__tel {
  color: var(--color-secondary-text);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.contacts__tel:hover {
  color: var(--color-text-orange);
}

.contacts__tel:focus {
  color: var(--color-text-orange);
  outline: solid 1px var(--color-text-orange);
}

.contacts__mail {
  color: var(--color-secondary-text);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.contacts__mail:hover {
  color: var(--color-text-orange);
}

.contacts__mail:focus {
  color: var(--color-text-orange);
  outline: solid 1px var(--color-text-orange);
}

.contacts__btns {
  margin-bottom: 39px;
}

.btn-icon {
  width: 12px;
  height: 12px;
}

.contacts__btn-location:focus .btn-icon, .contacts__btn-franchise:focus .btn-icon {
  fill: var(--color-primary-text);
}

.contacts__btn-franchise {
  border: 2px solid var(--color-btn-default);
  width: 196px;
  background-color: rgba(0, 0, 0, 0);
  gap: 10px;
  margin-left: auto;
  margin-right: auto;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.contacts__btn-franchise:hover {
  border-color: var(--color-btn-hover_focus);
}

.contacts__btn-franchise:focus {
  border-color: var(--color-btn-hover_focus);
  color: var(--color-primary-text);
  outline: none;
}

.contacts__btn-franchise:disabled {
  border-color: var(--color-btn-disable);
}

.contacts__btn-location {
  width: 196px;
  gap: 10px;
  margin-bottom: 16px;
  margin-left: auto;
  margin-right: auto;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.contacts__btn-location:hover {
  background-color: var(--color-btn-hover_focus);
}

.contacts__btn-location:focus {
  background-color: var(--color-btn-hover_focus);
  color: var(--color-primary-text);
  outline: none;
}

.contacts__btn-location:disabled {
  background-color: var(--color-btn-disable);
}

@media screen and (min-width: 768px) {
  .contacts__cards {
    gap: 16px;
    margin-bottom: 60px;
    display: flex;
  }

  .contacts__block {
    flex-basis: calc(33.3333% - 10.6667px);
    padding: 40px 24px;
  }

  .contacts__block:not(:last-child), .contacts__block:last-child {
    margin-bottom: 0;
  }

  .contacts__category {
    border-radius: 6px;
    margin-bottom: 32px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.875;
  }

  .contacts__city {
    margin-bottom: 12px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.85714;
  }

  .contacts__adress {
    margin-bottom: 26px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.85714;
  }

  .contacts__schedule {
    margin-bottom: 32px;
    padding: 32px 0;
  }

  .contacts__working-days, .contacts__working-hours, .contacts__tel, .contacts__mail {
    font-size: 14px;
    font-weight: 500;
    line-height: 1.85714;
  }

  .contacts__btns {
    justify-content: center;
    gap: 16px;
    margin-bottom: 40px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .contacts__cards {
    gap: 32px;
    margin-bottom: 64px;
  }

  .contacts__block {
    flex-basis: calc(33.3333% - 21.3333px);
    padding: 40px;
  }

  .contacts__city {
    margin-bottom: 8px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.875;
  }

  .contacts__adress {
    margin-bottom: 32px;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.8125;
  }

  .contacts__working-days, .contacts__working-hours {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.875;
  }

  .contacts__tel, .contacts__mail {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.8125;
  }

  .contacts__btns {
    margin-bottom: 120px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 0;
  background: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4));
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.modal-test-btn {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.modal-open-icecream {
  width: 100px;
  background-color: #aec6a5;
}

.modal-open-icecoffee {
  width: 100px;
  background-color: #efd478;
}

.modal-open-milkshake {
  width: 100px;
  background-color: #d6936d;
}

.modal-ing {
  opacity: 1;
  z-index: 100;
  width: 280px;
  background-color: var(--color-primary-text);
  border: 2px solid var(--color-btn-default);
  max-height: 100%;
  border-radius: 10px;
  padding: 28px 20px 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
