.modal-ing__item .gramm {
  width: 43px;
  height: 43px;
  background-color: var(--tint-icecream);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: -2px auto 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 2.33;
  display: flex;
  box-shadow: 0 6px 20px rgba(10, 136, 23, .25);
