}

.modal-ing__item-icecoffee {
  border: 2px solid var(--tint-icecoffee);
}

.modal-ing__item-icecoffee .gramm-icecoffee {
  background-color: var(--tint-icecoffee);
  box-shadow: 0 6px 20px rgba(153, 114, 23, .25);
}

.modal-ing__item-milkshake {
  border: 2px solid var(--tint-milkshake);
}

.modal-ing__item-milkshake .gramm-milkshake {
  background-color: var(--tint-milkshake);
  box-shadow: 0 6px 20px rgba(213, 117, 63, .25);
}

.ing {
  text-align: center;
}

